
.modal {
  transition: all 0.3s ease;
}
.modal.in {
  background-color: rgba(0,0,0,0.5);
}
.modal.zoom .modal-dialog {
  transform: scale(0.1);
  top: 300px;
  opacity: 0;
  transition: all 0.3s;
}
.modal.zoom.in .modal-dialog {
  transform: scale(1);
  transform: translate3d(0, -300px, 0);
  opacity: 1;
}



